import Vue from 'vue';
import Router from 'vue-router';
import Create from '@/components/Create.vue';
import ListView from '@/components/List.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ilan-ver',
      name: 'CreateView',
      component: Create
    },
    {
      path: '/ilanlarım',
      name:"ListView",
      component: ListView
    }
  ]
})



