<template>
  <div>
      <div class="row">
        <div class="twenty columns">
          <label for="exampleEmailInput">Pozisyon</label>
          <input placeholder="Örn. Front-End Developer" class="u-full-width" type="text">
        </div>
        <div class="row">
          <div class="twenty columns">
            <label for="exampleEmailInput">Adres</label>
            <select class="u-full-width" type="text">
              <option>İstanbul</option>
              <option>Eskişehir</option>
              <option>Kayseri</option>
              <option>Gaziantep</option>
            </select>
          </div>
        </div>
        <div class="row">
          <label>Genel Nitelikler</label>
          <textarea class="u-full-width"></textarea>
        </div>
        <div class="row">
          <b>Engelli İlanı</b>
          <input placeholder="Örn. Front-End Developer" class="" type="checkbox">
        </div>
      </div>
      <button @click="notifyMe">Yayınla</button>
  </div>
</template>

<script>
export default {
  name: "create",
  data() {
    return {};
  },
  methods: {
    notifyMe() {
      if (Notification.permission !== "granted")
        Notification.requestPermission();
      else {
        var notification = new Notification("Kariyer.net", {
          icon: "/static/img/icons/512kariyer.jpg",
          body: "İlanınız Yayınlandı !"
        });
      }
    }
  },
  mounted() {
    if (!Notification) {
      alert(
        "Desktop notifications not available in your browser. Try Chromium."
      );
      return;
    }

    if (Notification.permission !== "granted") Notification.requestPermission();
  }
};
</script>



